import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import { UserProvider } from "./context/User";
import { LoaderContextProvider } from "./context/LoaderContext";

ReactDOM.render(
  <LoaderContextProvider>
    <UserProvider>
      <React.StrictMode>
        <App />
      </React.StrictMode>
    </UserProvider>
  </LoaderContextProvider>,
  document.getElementById("root")
);

